/*Elements*/

*,
*::before,
*::after {
  box-sizing: border-box;
  white-space: nowrap;
}

/* typography*/

html {
  font-size: 62.5%; // 1rem = 10px
}

body {
  position: relative;
  font-family: "Courier New", Courier, monospace;
}

form {
  font-size: 2rem;
}

h1 {
  margin: 0;
}

ul {
  list-style-type: none;
  margin-left: 0;
  li {
    padding: 0;
    margin: 0;
  }
}

a {
  margin-left: 0;
  padding: 0;
}
